        <div class="gw-yacht__title">Вы успешно забронировали яхту - {{ getSelectedYacht.title }}</div>
        <div class="gw-success__caption">Копия чека отправлена на Вашу почту. <b>*Обьязательно сохраняйте чек</b></div>
        <table style="font-family: 'Verdana';border-spacing: 0;width: 600px;max-width: 100%;margin: 0 auto;background-color: #d2e6ff;padding: 24px;border-radius: 2px;">
            <tbody>
                <tr><th colspan="2" style="text-align: left;background-color: #fff;padding: 4px 8px;font-weight: 600;font-family: 'Arial', sans-serif;letter-spacing: 0.4px;border-radius: 2px;">Номер заказа</th></tr>
                <tr><td colspan="2" style="line-height:18px;padding: 10px 8px 16px 8px;letter-spacing:0.4px;font-size: 14px;">{{ checkout }}</td></tr>
                <tr><th colspan="2" style="text-align: left;background-color: #fff;padding: 4px 8px;font-weight: 600;font-family: 'Arial', sans-serif;letter-spacing: 0.4px;border-radius: 2px;">Название яхты</th></tr>
                <tr><td colspan="2" style="line-height:18px;padding: 10px 8px 16px 8px;letter-spacing:0.4px;font-size: 14px;">{{ getSelectedYacht.title }}</td></tr>
                <tr><th style="width:50%;border-right: 5px solid #d2e6ff;text-align: left;background-color: #fff;padding: 4px 8px;font-weight: 600;font-family: 'Arial', sans-serif;letter-spacing: 0.4px;border-radius: 2px;">Получение</th><th style="text-align: left;background-color: #fff;padding: 4px 8px;font-weight: 600;font-family: 'Arial', sans-serif;letter-spacing: 0.4px;border-radius: 2px;border-left: 5px solid #d2e6ff;">Возврат</th></tr>
                <tr><td style="line-height:18px;padding: 10px 8px 16px 8px;letter-spacing:0.4px;font-size: 14px;">{{ dayNames[new Date(start).getDay()] }}, {{ outputDateStart.split(' ')[0] }} {{`${outputDateStart.split(' ')[1] == 'август' ? 'августа' : outputDateStart.split(' ')[1].slice(0, -1) + 'я'}`}} c {{ outputDateStart.split(' ')[3]}}</td><td style="line-height:18px;padding: 10px 8px 16px 8px;letter-spacing:0.4px;font-size: 14px;">{{ dayNames[new Date(end).getDay()] }}, {{ outputDateEnd.split(' ')[0] }} {{`${outputDateEnd.split(' ')[1] == 'август' ? 'августа' : outputDateEnd.split(' ')[1].slice(0, -1) + 'я'}`}} до {{ outputDateEnd.split(' ')[3]}}</td></tr>
                <tr><th style="border-right: 5px solid #d2e6ff;text-align: left;background-color: #fff;padding: 4px 8px;font-weight: 600;font-family: 'Arial', sans-serif;letter-spacing: 0.4px;border-radius: 2px;">Оплачено</th><th style="text-align: left;background-color: #fff;padding: 4px 8px;font-weight: 600;font-family: 'Arial', sans-serif;letter-spacing: 0.4px;border-radius: 2px;border-left: 5px solid #d2e6ff;">2ая часть оплаты</th></tr>
                <tr>
                    <td style="line-height:18px;padding: 10px 8px 16px 8px;letter-spacing:0.4px;font-size: 14px;">
                        <template v-if="getSelectedYacht.prepay == 1">
                            {{ cost.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ") }}₽
                        </template>
                        <template v-else>
                            {{ (cost * 0.5).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ") }}₽
                        </template>
                    </td>
                    <td style="line-height:18px;padding: 10px 8px 16px 8px;letter-spacing:0.4px;font-size: 14px;">
                        <template v-if="getSelectedYacht.prepay != 1">
                            {{ (cost * 0.5).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ") }}₽
                        </template>
                        <template v-else>
                            0₽
                        </template>
                    </td>
                </tr>
                <tr><th colspan="2"  style="text-align: left;background-color: #fff;padding: 4px 8px;font-weight: 600;font-family: 'Arial', sans-serif;letter-spacing: 0.4px;border-radius: 2px;">Адрес получения и возврата</th></tr>
                <tr>
                    <td colspan="2" style="line-height:18px;padding: 10px 8px 16px 8px;letter-spacing:0.4px;font-size: 14px;">
                        <template v-if="getSelectedYacht.city == 'Ялта'">Yalta port</template>
                        <template v-if="getSelectedYacht.city == 'Севастополь'">Yacht club Admiral</template>
                        <template v-if="getSelectedYacht.city == 'Балаклава'">Balaklava Bay</template>
                        <template v-if="getSelectedYacht.city == 'Сочи'">Sochi Grand Marina</template>
                    </td>
                </tr>
                <tr>
                    <th colspan="2"  style="text-align: left;background-color: #fff;padding: 4px 8px;font-weight: 600;font-family: 'Arial', sans-serif;letter-spacing: 0.4px;border-radius: 2px;">Контакты капитана</th>
                </tr>
                <tr><td colspan="2" style="line-height:18px;padding: 10px 8px 16px 8px;letter-spacing:0.4px;font-size: 14px;">Ваши контакты переданы капитану. Капитан обязан связаться с Вами перед началом аренды, минимум за 3 часа.</td></tr>
