            <tfoot>
                <tr>
                    <th style="text-align: left;background-color: #fff;padding: 4px 8px;font-weight: 600;font-family: 'Arial', sans-serif;letter-spacing: 0.4px;border-radius: 2px;">Служба поддержки</th>
                </tr>
                <tr>
                    <td style="line-height:18px;padding: 10px 8px 16px 8px;letter-spacing:0.4px;font-size: 14px;">+7 964 900 28 06</td>
                </tr>
            </tfoot>
        </table>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    data: function() {
        return {
            dayNames: ['воскресенье', 'понедельник', 'вторник', 'среда', 'четверг', 'пятница', 'суббота'],
            outputDateStart: localStorage.getItem('GW_yacht_outputDateStart'),
            outputDateEnd: localStorage.getItem('GW_yacht_outputDateEnd'),
            cost: localStorage.getItem('GW_yacht_cost'),
            timeLength: JSON.parse(localStorage.getItem('GW_yacht_timelength')),
            start: localStorage.getItem('GW_yacht_start'),
            end: localStorage.getItem('GW_yacht_end'),
            params: localStorage.getItem('GW_yacht_params'),
            id: localStorage.getItem('GW_yacht_id'),
            checkout: localStorage.getItem('GW_checkout') ? localStorage.getItem('GW_checkout') : false,
            email: localStorage.getItem('GW_checkout_email'),
        }
    },
    computed: {
        ...mapGetters(['getSelectedYacht']),
    },
    mounted() {
        if (!this.id) this.$router.push({ path: '/' });
        this.getYacht(localStorage.getItem('GW_yacht_id'));

        let loc = '';
        if (this.getSelectedYacht.city == 'Ялта') loc = 'Yalta port';
        if (this.getSelectedYacht.city == 'Севастополь') loc = 'Yacht club Admiral';
        if (this.getSelectedYacht.city == 'Балаклава') loc = 'Balaklava Bay';
        if (this.getSelectedYacht.city == 'Сочи') loc = 'Sochi Grand Marina';

        let start = `${this.dayNames[new Date(this.start).getDay()]}, ${this.outputDateStart.split(' ')[0]} ${this.outputDateStart.split(' ')[1] == 'август' ? 'августа' : this.outputDateStart.split(' ')[1].slice(0, -1) + 'я'} c ${this.outputDateStart.split(' ')[3]}`;
        let end = `${this.dayNames[new Date(this.end).getDay()]}, ${this.outputDateEnd.split(' ')[0]} ${this.outputDateEnd.split(' ')[1] == 'август' ? 'августа' : this.outputDateEnd.split(' ')[1].slice(0, -1) + 'я'} до ${this.outputDateEnd.split(' ')[3]}`;

        fetch('https://pla-vay.ru/checkout-form.php', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                'email': this.email,
                'yachtName': this.getSelectedYacht.title,
                'yachtStart': start,
                'yachtEnd': end,
                'yachtPrepay': this.getSelectedYacht.prepay == 1 ? this.cost : this.cost * 0.5,
                'yachtTotal': this.getSelectedYacht.prepay == 1 ? 0 : this.cost * 0.5,
                'location': loc,
                'id': this.checkout
            })
        });

        window.localStorage.removeItem('GW_yacht_outputDateStart');
        window.localStorage.removeItem('GW_yacht_outputDateEnd');
        window.localStorage.removeItem('GW_yacht_cost');
        window.localStorage.removeItem('GW_yacht_timelength');
        window.localStorage.removeItem('GW_yacht_start');
        window.localStorage.removeItem('GW_yacht_end');
        window.localStorage.removeItem('GW_yacht_params');
        window.localStorage.removeItem('GW_yacht_id');
        window.localStorage.removeItem('GW_checkout');
        window.localStorage.removeItem('GW_checkout_email');
        window.localStorage.removeItem('GW_yacht_id');
    },
    methods: {
        ...mapActions(['getYacht']),
    }
}
</script>

