<template>
    <div class="gw-container gw-contacts">
        <div :class="`gw-status-ajax ${status && 'is-active'}`">
            Ваш запрос успешно отправлен
        </div>
        <div class="gw-bread">
            <router-link to="/">Поиск яхты</router-link>
            <b>-</b>
            <span>Контакты</span>
        </div>

        <div class="gw-contacts__wrapper">
            <div class="gw-contacts__item">
                <h1 class="gw-title gw-title--h1">Контакты</h1>
                <div class="gw-contacts__list">
                    <div>ул. Горького, 87/5, Сочи,<br>Краснодарский край, Россия, 354000</div>
                    <div>7 дней в неделю</div>
                    <div>Рабочий график: 08:00 - 22:00</div>
                    <a href="tel:+79649002806">+7 964 900 28 06</a><br>
                    <a href="mailto:help@pla-vay.ru">help@pla-vay.ru</a>
                </div>
            </div>
            <div class="gw-contacts__item">
                <h1 class="gw-title gw-title--h1">Есть вопрос?</h1>
                <div class="gw-contacts__form gw-form">
                    <label :class="`gw-form__group ${$v.name.$error && 'has-error'}`">
                        <div class="gw-form__group__label">Имя *</div>
                        <input type="text" class="gw-form__field" v-model.trim="name" v-on:blur="$v.name.$touch">
                    </label>
                    <label :class="`gw-form__group ${$v.secondName.$error && 'has-error'}`">
                        <div class="gw-form__group__label">Фамилия *</div>
                        <input type="text" class="gw-form__field" v-model.trim="secondName" v-on:blur="$v.secondName.$touch">
                    </label>
                    <label :class="`gw-form__group ${$v.email.$error && 'has-error'}`">
                        <div class="gw-form__group__label">Почта *</div>
                        <input type="text" class="gw-form__field" v-model.trim="email" v-on:blur="$v.email.$touch">
                    </label>
                    <div :class="`gw-form__group ${$v.phone.$error && 'has-error'}`">
                        <div class="gw-form__group__label">Телефон *</div>
                        <vue-tel-input v-model.trim="phone" v-bind="bindProps" v-on:blur="$v.phone.$touch"></vue-tel-input>
                    </div>
                    <label class="gw-form__message">
                        <div class="gw-form__group__label">Ваш вопрос</div>
                        <textarea class="gw-form__textarea" v-model="comment"></textarea>
                    </label>
                    <div class="gw-form__buttons">
                        <div class="button" @click="preSend">
                            <span>Отправить</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { required, email } from 'vuelidate/lib/validators';

export default {
    name: 'Contacts',
    data: function() {
        return {
            status: false,
            name: '',
            secondName: '',
            email: '',
            phone: '',
            comment: '',
            bindProps: {
                maxLen: 14,
                validCharactersOnly: true,
                mode: 'international',
                defaultCountry: 'RU',
                preferredCountries: ['RU'],
                placeholder: 'Введите номер',
                required: true,
                enabledCountryCode: false,
                enabledFlags: true,
                name: 'phone',
            },
        }
    },
    validations: {
        name: {
            required
        },
        secondName: {
            required
        },
        email: {
            email,
            required
        },
        phone: {
            required
        }
    },
    methods: {
        preSend() {
            this.$v.$touch();
            if (this.$v.$invalid) return;
            else {
                fetch('https://pla-vay.ru/contacts-form.php', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        'email': this.email,
                        'name': this.name,
                        'secondName': this.secondName,
                        'phone': this.phone,
                        'comment': this.comment,
                    })
                })
                .then(() => {
                    this.email = '';
                    this.name = '';
                    this.secondName = '';
                    this.phone = '';
                    this.comment = '';
                    this.$v.$reset();
                    this.status = true;
                    setTimeout(() => this.status = false, 4000);
                })
            }
        }
    }
}
</script>

