<script>
import { required, email } from 'vuelidate/lib/validators';

export default {
    name: 'Header',
    data: function() {
        return {
            status: false,
            bodyWidth: window.innerWidth,
            name: '',
            email: '',
            phone: '',
            comment: '',
            city: '',
            mmenu: false,
            bindProps: {
                maxLen: 14,
                validCharactersOnly: true,
                mode: 'international',
                defaultCountry: 'RU',
                preferredCountries: ['RU'],
                placeholder: 'Введите номер',
                required: true,
                enabledCountryCode: false,
                enabledFlags: true,
                name: 'phone',
            },
        }
    },
    validations: {
        name: {
            required
        },
        email: {
            email,
            required
        },
        phone: {
            required
        },
        city: {
            required
        }
    },
    methods: {
        openChat() {
            window.jivo_api.open();
        },
        showModal() {
            if (this.mmenu == true) {
                this.mmenu = false;
                setTimeout(() => {
                    this.$modal.show('modalForm');
                }, 200);
            } else {
                this.$modal.show('modalForm');
            }
        },
        preSend() {
            this.$v.$touch();
            if (this.$v.$invalid) return;
            else {
                fetch('https://pla-vay.ru/request-form.php', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        'email': this.email,
                        'name': this.name,
                        'city': this.city,
                        'phone': this.phone,
                        'comment': this.comment,
                    })
                })
                .then(() => {
                    this.email = '';
                    this.name = '';
                    this.city = '';
                    this.phone = '';
                    this.comment = '';
                    this.$v.$reset();
                    this.status = true;
                    setTimeout(() => this.status = false, 4000);
                })
            }
        },
    }
}
</script>

