import data from '@/assets/tt.json';

export default {
  state: {
    yachts: data,
    selectedYacht: null,
    sortedYachts: [],
    yachtsByCity: [],
    city: null,
    filters: {
      cost: [],
      passangers: [],
      kaut: [],
      spalnih_mest: [],
      sst: [],
    }
  },
  mutations: {
    setSortedYachts(state, yachts) {
      state.sortedYachts = yachts;
    },
    setYachtsByCity(state, yachts) {
      state.yachtsByCity = yachts;
    },
    setFilters(state, filters) {
      state.filters = filters;
    },
    setCity(state, city) {
      state.city = city;
    },
    selectYacht(state, yacht) {
      state.selectedYacht = yacht;
    }
  },
  actions: {
    sortYachts({ commit, getters }, payload) {
      let output = [];
      const start = new Date(payload.date.start).getTime();
      const end = new Date(payload.date.end).getTime();
      const filter = payload.filter;

      const sortedByCity = getters.getYachts.filter(yacht => yacht.city == payload.city);
      const triggerCity = payload.city == getters.getCity || getters.getCity == null ? false : true;

      let priceArr = [];
      let passangersArr = [];
      let kautArr = [];
      let spalnih_mestArr = [];
      let sstArr = [];

      for (let i = 0; i < sortedByCity.length; i++) {
        const item = sortedByCity[i];

        priceArr.push(item.cost);
        passangersArr.push(item.passangers);
        kautArr.push(item.kaut);
        spalnih_mestArr.push(item.spalnih_mest);
        sstArr.push(item.sst);
      }
      commit('setFilters', {
        cost: [Math.min(...priceArr), Math.max(...priceArr)],
        passangers: [Math.min(...passangersArr), Math.max(...passangersArr)],
        kaut: [Math.min(...kautArr), Math.max(...kautArr)],
        spalnih_mest: [Math.min(...spalnih_mestArr), Math.max(...spalnih_mestArr)],
        sst: [...new Set([].concat(...sstArr))]
      });
      commit('setYachtsByCity', sortedByCity);
      commit('setCity', payload.city);

      sortedByCity.forEach(item => {
        let trigger = true;
        const dates = item.booked;

        for (let i = 0; i < dates.length; i++) {
          const date = dates[i];
          const dateStart = new Date(date.start).getTime();
          const dateEnd = new Date(date.end).getTime();

          if (dateStart - start > 0 && dateEnd - start > 0
            && dateStart - end > 0 && dateEnd - end > 0) trigger = true;

          else if (start - dateStart > 0 && start - dateEnd > 0
            && end - dateStart > 0 && end - dateEnd > 0) trigger = true;

          else {
            trigger = false;
            break;
          }
        }

        if (trigger) output.push(item);
      });

      if (filter.cost.length && !triggerCity) {
        output = output.filter(item => {
          const cost = Number(item.cost);
          const passangers = Number(item.passangers);
          const kaut = Number(item.kaut);
          const spalnih_mest = Number(item.spalnih_mest);
          const sst = item.sst;
          
          if (cost >= Number(filter.cost[0]) && cost <= Number(filter.cost[1])
              && passangers >= Number(filter.passangers[0]) && passangers <= Number(filter.passangers[1])
              && kaut >= Number(filter.kaut[0]) && kaut <= Number(filter.kaut[1])
              && spalnih_mest >= Number(filter.spalnih_mest[0]) && spalnih_mest <= Number(filter.spalnih_mest[1])) {

              if (filter.sst) {
                if (sst.filter(el => filter.sst.includes(el)).length == filter.sst.length) {
                  return item;
                }
              } else {
                return item;
              }
          }
        });
      }

      commit('setSortedYachts', output);
    },
    getYacht({ commit, getters }, id) {
      commit('selectYacht', getters.getYachts[id]);
    },
    clearSelectedYacht({ commit }) {
      commit('selectYacht', null)
    }
  },
  getters: {
    getSortedYachts(state) {
      return state.sortedYachts;
    },
    getYachts(state) {
      return state.yachts;
    },
    getYachtsByCity(state) {
      return state.yachtsByCity;
    },
    getFilters(state) {
      return state.filters;
    },
    getCity(state) {
      return state.city;
    },
    getSelectedYacht(state) {
      return state.selectedYacht;
    }
  }
};
