<script>
import md5 from 'js-md5';
import { required, email } from 'vuelidate/lib/validators';
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'Checkout',
    data: function() {
        return {
            dayNames: ['воскресенье', 'понедельник', 'вторник', 'среда', 'четверг', 'пятница', 'суббота'],
            outputDateStart: localStorage.getItem('GW_yacht_outputDateStart'),
            outputDateEnd: localStorage.getItem('GW_yacht_outputDateEnd'),
            cost: localStorage.getItem('GW_yacht_cost'),
            timeLength: JSON.parse(localStorage.getItem('GW_yacht_timelength')),
            start: localStorage.getItem('GW_yacht_start'),
            end: localStorage.getItem('GW_yacht_end'),
            params: localStorage.getItem('GW_yacht_params'),
            bindProps: {
                maxLen: 14,
                validCharactersOnly: true,
                mode: 'international',
                defaultCountry: 'RU',
                preferredCountries: ['RU'],
                placeholder: 'Введите номер',
                required: true,
                enabledCountryCode: false,
                enabledFlags: true,
                name: 'phone',
            },
            name: '',
            secondName: '',
            email: '',
            phone: '',
            comment: '',
            urlParams: '',
            mdString: '',
            payId: `id${new Date().getTime()}`
        }
    },
    validations: {
        name: {
            required
        },
        secondName: {
            required
        },
        email: {
            email,
            required
        },
        phone: {
            required
        }
    },
    components: {
        
    },
    computed: {
        ...mapGetters(['getSelectedYacht']),
        id() {
            return this.$route.params.id;
        }
    },
    beforeCreate() {
        if (!localStorage.getItem('GW_yacht_id')) this.$router.push({ path: '/' });
    },
    mounted() {
        this.getYacht(localStorage.getItem('GW_yacht_id'));
    },
    methods: {
        ...mapActions(['getYacht']),
        showMap() {
            this.$modal.show('map');
        },
        preSend() {
            this.$v.$touch();
            if (this.$v.$invalid) return;
            else {
                const params = {
                    'email': `${this.email}`,
                    'name': `${this.name}`,
                    'secondName': `${this.secondName}`,
                    'phone': `${this.phone}`,
                    'yachtName': `${this.getSelectedYacht.title}`,
                    'yachtType': `${this.getSelectedYacht.type}`,
                    'yachtStart': `${this.outputDateStart}`,
                    'yachtEnd': `${this.outputDateEnd}`,
                    'yachtPrepay': this.getSelectedYacht.prepay == 1 ? this.cost : this.cost * 0.5,
                    'yachtTotal': this.cost,
                    'yachtImage': `https://pla-vay.ru/images/${this.getSelectedYacht.main_image}`,
                    'comment': `${this.comment}`,
                };
                this.urlParams = Object.keys(params).map((k, idx) => {
                    let prefix = '';
                    if (idx === 0) {
                        prefix = '?';
                    }
                    return prefix + k + '=' + encodeURIComponent(params[k]);
                }).join('&');

                this.mdString = md5([
                    `${this.getSelectedYacht.prepay == 1 ? this.cost : this.cost * 0.5}`, 
                    this.payId, 
                    332, 
                    'RUB', 
                    this.getSelectedYacht.title, 
                    'ff4fed598e8584fef2c38436c169b2d8'
                    ].join('|'))
                window.localStorage.setItem('GW_checkout', this.payId);
                window.localStorage.setItem('GW_checkout_email', this.email);
                setTimeout(() => this.$refs['sendPay'].click(), 200)
            }
        }
    },
}
</script>

