                                        <polygon style="fill:#F0F0F0;" points="0,85.33 0,199.107 0,312.885 0,426.662 512,426.662 512,312.885 512,199.107 512,85.33 "/>
                                        <rect y="85.333" style="fill:#0052B4;" width="512" height="341.337"/>
