                            </div>
                            <div class="gw-yacht__poleznyainfo__item"  v-if="timeLength">
                                <div class="gw-yacht__poleznyainfo__head">
                                    Получение
                                </div>
                                <div class="gw-yacht__poleznyainfo__body">
                                    <p>{{ dayNames[new Date(dateFrom.start).getDay()] }}, {{ outputDateStart.split(' ')[0] }} {{`${outputDateStart.split(' ')[1] == 'август' ? 'августа' : outputDateStart.split(' ')[1].slice(0, -1) + 'я'}`}} в {{ outputDateStart.split(' ')[3]}}</p>
                                </div>
                            </div>
                            <div class="gw-yacht__poleznyainfo__item"  v-if="timeLength">
                                <div class="gw-yacht__poleznyainfo__head">
                                    Возврат
                                </div>
                                <div class="gw-yacht__poleznyainfo__body">
                                    <p>{{ dayNames[new Date(dateFrom.end).getDay()] }}, {{ outputDateEnd.split(' ')[0] }} {{`${outputDateEnd.split(' ')[1] == 'август' ? 'августа' : outputDateEnd.split(' ')[1].slice(0, -1) + 'я'}`}} до {{ outputDateEnd.split(' ')[3]}}</p>
                                </div>
                            </div>
                            <div class="gw-yacht__poleznyainfo__item"  v-if="timeLength">
                                <div class="gw-yacht__poleznyainfo__head">
                                    Отмена / Условия оплаты
                                </div>
                                <div class="gw-yacht__poleznyainfo__body">
                                    <p><template v-if="getSelectedYacht.prepay == 1"><b>100%&nbsp;</b> </template><template v-else><b>50%&nbsp;</b></template> предоплата бронирования.</p>
                                    <p>Вы можете бесплатно отменить бронирование до <b v-if="outputDateStart">{{ outputDateStart.split(' ')[0] }} {{`${outputDateStart.split(' ')[1] == 'август' ? 'августа' : outputDateStart.split(' ')[1].slice(0, -1) + 'я'}`}}</b>.</p>
                                    <template v-if="getSelectedYacht.prepay != 1"><p>Вторую часть оплаты Вы оплачиваете капитану, после приема яхты.</p></template>
                                    <p><b>* капитан обязан выдать вам чек об оплате.</b></p>
                                </div>
                            </div>
                            <div class="gw-yacht__poleznyainfo__item"  v-if="timeLength">
                                <div class="gw-yacht__poleznyainfo__head">
                                    Информация о
                                    <template v-if="getSelectedYacht.city == 'Ялта'">Yalta port</template>
                                    <template v-if="getSelectedYacht.city == 'Севастополь'">Yacht club Admiral</template>
                                    <template v-if="getSelectedYacht.city == 'Балаклава'">Balaklava Bay</template>
                                    <template v-if="getSelectedYacht.city == 'Сочи'">Sochi Grand Marina</template>
                                </div>
                                <div class="gw-yacht__poleznyainfo__body">
                                    <template v-if="getSelectedYacht.city == 'Ялта'">
                                        <p>Yalta port расположен в городе Ялта, Россия. От центра Ялты 1 километр.</p>
                                        <p>Yalta port предлагает широкий спектр услуг для яхтсменов, включая такие услуги, как Автостоянка и Заправочная станция.</p>
                                        <p>Поблизости есть рестораны.</p>
                                        <p>Все причалы имеют подключение к следующим услугам водоснабжение и электричество.</p>
                                    </template>
                                    <template v-if="getSelectedYacht.city == 'Севастополь'">
                                        <p>Yacht club Admiral расположен в городе Севастополь, Россия.</p>
                                        <p>Yacht club Admiral предлагает широкий спектр услуг, такие как автостоянка, заправочная станция и туалет.</p>
                                        <p>Поблизости есть рестораны.</p>
                                        <p>Все причалы имеют подключение к следующим услугам: водоснабжение и электричество.</p>
                                    </template>
                                    <template v-if="getSelectedYacht.city == 'Балаклава'">
                                        <p>Balaklava Bay расположен возле города Севастополь, Россия.</p>
                                        <p>Sochi Grand Marina предлагает широкий спектр услуг, такие как автостоянка, заправочная станция и туалет.</p>
                                        <p>Поблизости есть рестораны.</p>
                                        <p>Все причалы имеют подключение к следующим услугам: водоснабжение и электричество.</p>
                                    </template>
                                    <template v-if="getSelectedYacht.city == 'Сочи'">
                                        <p>Sochi Grand Marina расположена возле города Сочи, Россия. Sochi Grand Marina предоставляет 300 причалов для яхт.</p>
                                        <p>Она расположена в 27 километрах от ближайшего аэропорта.</p>
                                        <p>Sochi Grand Marina предлагает широкий спектр услуг, такие услуги, как автостоянка, заправочная станция и туалет.</p>
                                        <p>Поблизости есть рестораны.</p>
                                        <p>Все причалы имеют подключение к следующим услугам: водоснабжение и электричество.</p>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import '@splidejs/splide/dist/css/themes/splide-default.min.css';
import { Splide, SplideSlide } from '@splidejs/vue-splide';
import VueGoodshareTelegram from 'vue-goodshare/src/providers/Telegram.vue';
import VueGoodshareVkontakte from 'vue-goodshare/src/providers/Vkontakte.vue';
import VueGoodshareViber from 'vue-goodshare/src/providers/Viber.vue';
import VueGoodshareWhatsApp from 'vue-goodshare/src/providers/WhatsApp.vue';
import Tinybox from 'vue-tinybox';
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'Yacht',
    data: function() {
        return {
            bodyWidth: window.innerWidth,
            share: false,
            activeSlide: 0,
            gIndex: 0,
            gOpen: false,
            attrs: [
                {
                key: 'today',
                highlight: {
                    backgroundColor: '#ff8080',
                },
                dates: new Date(2018, 0, 1)
                }
            ],
            dateFrom: {},
            reservedDates: [],
            outputDateStart: '',
            outputDateEnd: '',
            minDate: null,
            masks: {
                inputDateTime24hr: 'DD MMMM  HH:mm',
            },
            paramsTrigger: 0,
            params: {},
            dayNames: ['воскресенье', 'понедельник', 'вторник', 'среда', 'четверг', 'пятница', 'суббота'],
            timeLength: null,
        }
    },
    components: {
        Splide,
        SplideSlide,
        Tinybox,
        VueGoodshareTelegram,
        VueGoodshareVkontakte,
        VueGoodshareViber,
        VueGoodshareWhatsApp,
    },
    computed: {
        ...mapGetters(['getSelectedYacht']),
        id() {
            return this.$route.params.id;
        }
    },
    created() {
        this.minDate = this.makeMinDate().start;
        this.clearSelectedYacht();
        for (let p in this.$route.query) {
            const key = p;
            const value = this.$route.query[p];
            let day = null;
            switch(key) {
                case 'start':
                    day = String(value.split('-')[2].split('T')[0]);
                    day = day.length < 2 ? '0' + day : day;
                    this.dateFrom.start = `${value.split('-')[0]}-${value.split('-')[1]}-${day}T${value.split('T')[1]}`;
                    break;
                case 'end':
                    day = String(value.split('-')[2].split('T')[0]);
                    day = day.length < 2 ? '0' + day : day;
                    this.dateFrom.end = `${value.split('-')[0]}-${value.split('-')[1]}-${day}T${value.split('T')[1]}`;
                    break;
            }
        }
    },
    mounted() {
        this.getYacht(this.id).then(() => {
            this.checkReservedDate();
            this.minDate = this.makeMinDate().start;
            const outTime = ((new Date(this.dateFrom.end) - new Date(this.dateFrom.start)) / 3.6e+6).toFixed();
            if (outTime / 24 >= 1) {
                const days = Math.round(outTime / 24);
                const hours = outTime - days * 24;
                this.timeLength = {
                    days: hours > 7 ? days + 1 : days,
                    hours: hours < 1 || hours > 7 ? 0 : String(hours).split('.')[0],
                    min: String(hours).split('.')[1] || 0
                };
            } else if (outTime / 8 >= 1) {
                const days = 1;
                this.timeLength = {
                    days: days,
                    hours: 0,
                    min: 0
                };
            } else {
                this.timeLength = {
                    days: 0,
                    hours: String(outTime).split('.')[0],
                    min: String(outTime).split('.')[1] || 0
                };
            }   
            const calendar = this.$refs.calendar;
            this.outputDateStart = calendar.inputValues[0];
            this.outputDateEnd = calendar.inputValues[1];
        });

        document.body.addEventListener('click', (event) => {
            if (!event.target.classList.contains('gw-yacht__tools__share') 
                || !event.target.classList.contains('button-social')
                || !event.target.classList.contains('kqq')
                ) {
                    if (!event.target.closest('.kqq')) {
                        if (document.querySelector('.gw-yacht__tools__share') && document.querySelector('.gw-yacht__tools__share').classList.contains('is-active')) {
                            this.share = false;
                        }
                    }
            }
        });
    },
    watch: {
        activeSlide() {
            this.$refs.primary.go(`${this.activeSlide}`);
            this.gIndex = this.activeSlide;
        },
        dateFrom: function() {
            this.checkDate();

            const start = new Date(this.dateFrom.start);
            const end = new Date(this.dateFrom.end);
            const checker = this.makeMinDate();

            let startMonth = start.getMonth() + 1;
            startMonth = String(startMonth).length < 2 ? '0' + startMonth : startMonth;
            let startHours = start.getHours();
            startHours = String(startHours).length < 2 ? '0' + startHours : startHours;
            let startMinutes = start.getMinutes();
            startMinutes = String(startMinutes).length < 2 ? '0' + startMinutes : startMinutes;

            let endMonth = start.getMonth() + 1;
            endMonth = String(endMonth).length < 2 ? '0' + endMonth : endMonth;
            let endHours = end.getHours();
            endHours = String(endHours).length < 2 ? '0' + endHours : endHours;
            let endMinutes = end.getMinutes();
            endMinutes = String(endMinutes).length < 2 ? '0' + endMinutes : endMinutes;

            this.params.start = `${start.getFullYear()}-${startMonth}-${start.getDate()}T${startHours}:${startMinutes}`;
            this.params.end = `${end.getFullYear()}-${endMonth}-${end.getDate()}T${endHours}:${endMinutes}`;

            const qSearch = new URL(location.href).searchParams;
            if (qSearch.get('start') == null 
                && qSearch.get('end') == null) {
                if (start.getTime() != new Date(checker.start).getTime()
                && end.getTime() != new Date(checker.end).getTime()) {
                    this.paramsTrigger++;
                }
            } else if (new Date(qSearch.get('start')).getTime() != start.getTime()
                || new Date(qSearch.get('end')).getTime() != end.getTime()) {
                this.paramsTrigger++;
            }
        },

        paramsTrigger: function() {
            let urlParams = '?';
            this.page = 1;

            for (const property in this.params) {
                urlParams += `${property}=${encodeURIComponent(this.params[property])}&`;
            }
            urlParams = urlParams.slice(0, -1);
            window.history.replaceState(null, null, urlParams);
        }
    },
    methods: {
        ...mapActions(['getYacht', 'clearSelectedYacht']),
        slideThumb(event, index) {
            this.$refs.primary.go(`${index}`)
        },
        moved( splide, newIndex ) {
            this.activeSlide = newIndex;
        },
        slideNext() {
            this.$refs.primary.go('>');
        },
        slidePrev() {
            this.$refs.primary.go('<');
        },
        openGallery() {
            this.gIndex = this.activeSlide;
            this.gOpen = true;
        },
        showMap() {
            this.$modal.show('map');
        },
        checkReservedDate() {
            let trigger = true;
            this.reservedDates = [];
            const date = this.dateFrom;
            const dateFrom = new Date(date.start);
            const dateEnd = new Date(date.end);
            let bookedDates = this.getSelectedYacht.booked;
            bookedDates = [].concat(...bookedDates);

            let reserved = [];

            for (let i = 0; i < bookedDates.length; i++) {
                const bookedDate = bookedDates[i];
                let bookedStart = bookedDate.start;
                let bookedEnd = bookedDate.end;
                let nextDay = false;

                if (dateFrom.getDate() < dateEnd.getDate()) nextDay = true;

                let checkedStart = new Date(new Date(bookedStart).setHours(new Date(dateFrom).getHours()));
                checkedStart = new Date(new Date(checkedStart).setMinutes(new Date(dateFrom).getMinutes()));

                checkedStart = checkedStart.getTime();

                let checkedEnd = new Date(new Date(bookedEnd).setHours(new Date(dateEnd).getHours()));
                checkedEnd = new Date(new Date(checkedEnd).setMinutes(new Date(dateEnd).getMinutes()));

                if (nextDay) checkedEnd = new Date(new Date(checkedEnd).setDate(new Date(checkedEnd).getDate() + 1));

                checkedEnd = checkedEnd.getTime();

                bookedStart = new Date(bookedStart).getTime();
                bookedEnd = new Date(bookedEnd).getTime();

                if (bookedStart - checkedStart > 0 && bookedEnd - checkedStart > 0
                    && bookedStart - checkedEnd > 0 && bookedEnd - checkedEnd > 0) {
                    trigger = true;
                    } else if (checkedStart - bookedStart > 0 && checkedStart - bookedEnd > 0
                    && checkedEnd - bookedStart > 0 && checkedEnd - bookedEnd > 0) {
                    trigger = true;
                    } else {
                    trigger = false;
                }

                if (!trigger) {
                    reserved.push({
                        start: bookedDate.start.split(' ')[0],
                        end: bookedDate.end.split(' ')[0],
                    })
                }

            }

            reserved = reserved.filter((item1, index) => {
                return reserved.findIndex(item2 => item2.start == item1.start && item2.end == item1.end) === index
            });
            
            let clone = [...reserved];

            for (let i = 0; i < reserved.length; i++) {
                const rItem = reserved[i];

                const yachtBooked = this.getSelectedYacht.booked;

                if (yachtBooked.findIndex(item => item.start.split(' ')[0] == rItem.start && item.end.split(' ')[0] == rItem.end) != -1) {
                    continue;
                } else {
                    delete clone[i]
                    break;
                }
            }

            this.reservedDates = clone;
        },
        makeMinDate() {
            const dateFrom = new Date().getMonth() + 1 <= 8 && new Date().getDate() < 21 ? new Date('2021-08-21T08:00') : new Date();
            const dateTo = new Date().getMonth() + 1 <= 8 && new Date().getDate() < 21 ? new Date('2021-08-21T11:00') : new Date();
        
            if (dateFrom.getDate() < 12) {
                dateFrom.setHours(dateFrom.getHours() + 4);
                dateFrom.setMinutes('00');
                dateTo.setHours(dateTo.getHours() + 7);
                dateTo.setMinutes('00');
            }

            if (dateFrom.getHours() > 20 || dateFrom.getHours() < 8) {
                if (dateFrom.getHours() > 20) {
                dateFrom.setDate(dateFrom.getDate() + 1);
                dateTo.setDate(dateFrom.getDate());
                }
        
                dateFrom.setHours(8);
                dateFrom.setMinutes(0);
                dateFrom.setSeconds(0);
        
                dateTo.setHours(11);
                dateTo.setMinutes(0);
                dateTo.setSeconds(0);
            }

            if (dateFrom.getMinutes() > 30) {
                dateFrom.setMinutes('30');
                dateTo.setMinutes('30');
            } else if (dateFrom.getMinutes() < 30) {
                dateFrom.setMinutes('00');
                dateTo.setMinutes('00');
            }
        
            return {
                start: dateFrom,
                end: dateTo,
            }
        },
        checkDate() {
            if (this.dateFrom == null) return;

            const calendar = this.$refs.calendar;

            const ps = new Date(this.dateFrom.start).getTime();
            const pe = new Date(this.dateFrom.end).getTime();
            let start = new Date(this.dateFrom.start);
            let end = new Date(this.dateFrom.end);

            let cDateChecked = null;
            const cDate = new Date().getTime() < new Date('2021-08-21T00:00').getTime() ? new Date('2021-08-21T00:00') : new Date();
            const cHour = cDate.getHours();
            const cMinutes = cDate.getMinutes();

            cDateChecked = new Date(new Date(cDate).setHours(cHour + 3));
            if (cMinutes < 15) cDateChecked = new Date(new Date(cDateChecked).setMinutes(0));
            if (cMinutes >= 15 && cMinutes < 45) cDateChecked = new Date(new Date(cDateChecked).setMinutes(30));
            if (cMinutes >= 45) {
                cDateChecked = new Date(cDateChecked.setHours(cHour + 4));
                cDateChecked = new Date(cDateChecked.setMinutes(0));
            }

            if (cDateChecked.getHours() < 8) cDateChecked = new Date(cDateChecked.setHours(8));
            if (cDateChecked.getHours() > 18) {
                cDateChecked = new Date(cDateChecked.setHours(8))
                cDateChecked = new Date(cDateChecked.setDate(cDateChecked.getDate() + 1));
            }

            if (start.getTime() < cDateChecked.getTime()) {
                start = new Date(cDateChecked);
                end = new Date(new Date(cDateChecked).setTime(start.getTime() + 10800000));
            }

            if (start.getHours() < 8) start = new Date(start.setHours(8));
            if (start.getHours() > 21) start = new Date(start.setHours(21));

            if (start.getMinutes() > 30) {
                start.setMinutes('30');
                end.setMinutes('30');
            } else if (start.getMinutes() < 30) {
                start.setMinutes('00');
                end.setMinutes('00');
            }

            if (end.getTime() <= start.getTime()) {
                end = new Date(start.getTime() + 10800000);
            } else if (end.getTime() - start.getTime() < 10800000) {
                end = new Date(end.getTime() + (10800000 - (end.getTime() - start.getTime())));
            }

            const outTime = (new Date(this.dateFrom.end) - new Date(this.dateFrom.start)) / 3.6e+6;
            if (outTime / 24 >= 1) {
                const days = Math.round(outTime / 24);
                const hours = outTime -  days * 24;
                this.timeLength = {
                days: days,
                hours: String(hours).split('.')[0],
                min: String(hours).split('.')[1] || 0
                };
            } else if (outTime / 12 >= 1) {
                const days = 1;
                this.timeLength = {
                days: days,
                hours: 0,
                min: 0
                };
            } else {
                this.timeLength = {
                days: 0,
                hours: String(outTime).split('.')[0],
                min: String(outTime).split('.')[1] || 0
                };
            }

            if (ps != start.getTime() || pe != end.getTime()) {

                console.log(start)
                this.dateFrom = {
                    start: start,
                    end: end
                };

                calendar.updateValue(this.dateFrom)
            }
            setTimeout(() => {
                this.outputDateStart = calendar.inputValues[0];
                this.outputDateEnd = calendar.inputValues[1];
            }, 100);
        },
        goToCheckout() {
            localStorage.setItem('GW_yacht_id', this.getSelectedYacht.id);
            localStorage.setItem('GW_yacht_start', this.dateFrom.start);
            localStorage.setItem('GW_yacht_end', this.dateFrom.end);
            localStorage.setItem('GW_yacht_timelength', JSON.stringify(this.timeLength));
            localStorage.setItem('GW_yacht_outputDateStart', this.outputDateStart);
            localStorage.setItem('GW_yacht_outputDateEnd', this.outputDateEnd);
            localStorage.setItem('GW_yacht_cost', ((Number(this.getSelectedYacht.cost) * (Number(this.timeLength.days) * 8)) + (Number(this.getSelectedYacht.cost) * Number(this.timeLength.hours)) + (Number(this.getSelectedYacht.cost) * Number(this.timeLength.min * 0.1))));
            localStorage.setItem('GW_yacht_params', window.location.href.split('?')[1]);

            this.$router.push({ name: 'Checkout' });
        }
    },
}
</script>

<style lang="scss">
    .vue-affix {
        position: relative;
    }

    .affix {
        position: fixed;
